<template>
  <!--
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">

  </div>
  -->
  <div style="padding: 10px">
    <div style="margin: 10px">
      <el-row>
        <!--
         <el-button>默认按钮</el-button>
        <el-button type="info">信息按钮</el-button>
        <el-button type="warning">警告按钮</el-button>
        <el-button type="danger">危险按钮</el-button>
        -->
        <el-button type="primary" @click="add">新增</el-button>
        <el-button type="primary">导入</el-button>
        <el-button type="primary">导出</el-button>
      </el-row>
    </div>
    <div style="margin: 10px">
            <el-input v-model="search" placeholder="查询" style="width: fit-content" clearable></el-input>
            <el-button type="primary" @click="load" style="margin-left: 5px">查询</el-button>
    </div>
    <el-table
            :data="tableData"
            stripe
            border
            style="width: 95%"
            size="medium">
      <el-table-column
              prop="id"
              label="id"
              width="100"
              sortable>
      </el-table-column>
      <el-table-column
              prop="calling"
              label="被屏蔽的电话"
              width="180">
      </el-table-column>
      <el-table-column
              prop="dsc"
              label="描述"
              width="180">
      </el-table-column>
      <el-table-column
              prop="createdate"
              label="创建时间"
              width="300"
              sortable>
      </el-table-column>
      <el-table-column
              fixed="right"
              label="操作"
             >
        <template #default="scope">

          <el-button @click="handleEdit(scope.row)" type="info" size="small">编辑</el-button>
          <el-popconfirm title="确定删除吗？" @confirm="handleDel(scope.row.id)">
            <template #reference>
              <el-button  type="danger" size="small">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin: 10px">

      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[5, 10, 20]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
              >
      </el-pagination>

      <el-dialog
              title="提示"
              v-model="dialogVisible"
              width="30%"
              :before-close="handleClose">
        <div style="margin: 20px;"></div>
        <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
          <el-form-item label="屏蔽号码">
            <el-input v-model="formLabelAlign.calling"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input v-model="formLabelAlign.dsc"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="save">确 定</el-button>
          </span>
        </template>
      </el-dialog>
    </div>
  </div>


</template>

<script>
  import request from "@/utils/request";

  export default {
    created() {
        this.load();
      },
    data() {
      return {
        dialogVisible:false,
        formLabelAlign:{},
        search:"",
        currentPage: 1,
        total:0,
        pageSize: 5,
        tableData: []
      }
    },
    methods: {
      add(){
        this.dialogVisible=true
        this.formLabelAlign={}
      },
      save(){
        if(this.formLabelAlign.id){
          request.put("/blacklist",this.formLabelAlign).then(res =>{
            console.log(res)
            if(res.code==="0"){
              this.$message({
                type:"success",
                message:"更新成功"
              })
            }else {
              this.$message({
                type:"error",
                message:"更新失败"
              })
            }
            this.load()
            this.dialogVisible=false
          })
        }else {
          request.post("/blacklist",this.formLabelAlign).then(res =>{
            console.log(res)
            if(res.code==="0"){
              this.$message({
                type:"success",
                message:"添加成功"
              })
            }else {
              this.$message({
                type:"error",
                message:"添加失败"
              })
            }
            this.load()
            this.dialogVisible=false
          })
        }
      },
      load(){
        console.log(this)
        request.get("/blacklist",{
          params:{
            pageNum:this.currentPage,
            pageSize:this.pageSize,
            search:this.search
          }
        }).then(res =>{
          console.log(res)
          this.tableData=res.data.records
          this.total=res.data.total
        }).catch(function(err) {
          console.log(err)
        })

      },

      handleEdit(row) {
        console.log(row);
        this.formLabelAlign=JSON.parse(JSON.stringify(row))
        this.dialogVisible=true
      },
      handleDel(row) {
        console.log(row);
        request.delete("/blacklist/"+row).then(res=>{
          console.log(res)
          if(res.code==="0"){
            this.$message({
              type:"success",
              message:"添加成功"
            })
          }else {
            this.$message({
              type:"error",
              message:"添加失败"
            })
          }
          this.load()
        })

      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pageSize=val
        this.load()
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage=val
        this.load()
      }
    }
  }

</script>
